// src/app/register/page.tsx
'use client'

import { useState } from 'react'
import { useRouter } from 'next/navigation'
import Link from 'next/link'
import { Button } from '@/components/ui/button'
import { Input } from '@/components/ui/input'
import { Label } from '@/components/ui/label'
import { Github } from 'lucide-react'
// Não precisamos do useAuth aqui, pois o registro apenas redireciona para o login.

export default function RegisterPage() {
  const router = useRouter()
  const [formData, setFormData] = useState({
    email: '',
    password: '',
  })
  const [error, setError] = useState('')
  const [loading, setLoading] = useState(false)

  const handleChange = (e: React.ChangeEvent<HTMLInputElement>) => {
    setFormData(prev => ({
      ...prev,
      [e.target.name]: e.target.value
    }))
  }

  const handleRegister = async (e: React.FormEvent) => {
    e.preventDefault()
    setError('')

    if (formData.password.length < 8) {
      setError('A senha deve ter pelo menos 8 caracteres')
      return
    }

    setLoading(true)

    try {
      // 1. Requisição para o endpoint de REGISTRO do Django
      const response = await fetch(`${process.env.NEXT_PUBLIC_API_URL}/api/auth/register/`, { // Ajuste o endpoint se necessário
        method: 'POST',
        headers: {
          'Content-Type': 'application/json',
        },
        body: JSON.stringify({
          email: formData.email,
          password: formData.password,
        }),
      })

      if (response.ok) {
        // Registro bem-sucedido: Redireciona o usuário para fazer login
        alert('Conta criada com sucesso! Faça login para continuar.');
        router.push('/login') 
        
      } else {
        const data = await response.json()
        // Trata erros específicos de validação do Django
        setError(data.message || 'Erro ao criar conta. Verifique os dados.');
      }
    } catch (err) {
      setError('Erro ao conectar com o servidor');
    } finally {
      setLoading(false);
    }
  }

  // ... restante do componente RegisterPage (handleSocialLogin, JSX) ...
  // Inclua a renderização do seu JSX fornecido
  // ...
  return (
    <div className="min-h-screen bg-white">
      {/* Header (copiado do LoginPage) */}
      <header className="bg-black text-white py-4 px-8">
        <nav className="flex items-center justify-between max-w-7xl mx-auto">
          <div className="font-bold text-2xl tracking-tight font-mono">Dostanza</div>
          <div className="hidden md:flex items-center gap-8 text-sm">
            <Link href="/explore" className="hover:opacity-80">Explore</Link>
            <Link href="/progress" className="hover:opacity-80">Progresso</Link>
            <Link href="/ranking" className="hover:opacity-80">Ranking</Link>
            <Link href="/community" className="hover:opacity-80">Comunidade</Link>
            <Link href="/achievements" className="hover:opacity-80">Donquistas</Link>
          </div>
          <button className="md:hidden">
            <div className="w-6 h-0.5 bg-white mb-1.5"></div>
            <div className="w-6 h-0.5 bg-white mb-1.5"></div>
            <div className="w-6 h-0.5 bg-white"></div>
          </button>
        </nav>
      </header>

      {/* Main Content */}
      <div className="max-w-7xl mx-auto px-8 py-16">
        <div className="grid md:grid-cols-2 gap-16 items-center">
          {/* Left Side - Decorative (copiado do LoginPage) */}
          <div>
            <h1 className="text-5xl font-bold mb-4 text-black">
              Como se estivesse
            </h1>
            <h2 className="text-5xl font-bold text-[#d946ef]">Jogando</h2>
            
            <div className="relative mt-16 h-96">
              {/* Decorative SVG/Divs... */}
            </div>
          </div>

          {/* Right Side - Register Form */}
          <div className="bg-gray-50 rounded-3xl p-8 shadow-sm">
            <div className="mb-8">
              <h3 className="text-3xl font-bold font-pixel">
                <span className="bg-gradient-to-r from-[#d946ef] via-[#ec4899] to-[#a855f7] bg-clip-text text-transparent">
                  Costanza
                </span>
                {' '}<span className="text-gray-800">x</span>{' '}
                <span className="bg-gradient-to-r from-[#f97316] via-[#fb923c] to-[#f87171] bg-clip-text text-transparent">
                  UniAp
                </span>
              </h3>
            </div>

            <form onSubmit={handleRegister} className="space-y-6">
              <div>
                <Label htmlFor="email" className="text-black font-medium">Email</Label>
                <Input
                  id="email"
                  name="email"
                  type="email"
                  value={formData.email}
                  onChange={handleChange}
                  className="mt-2 bg-white border-gray-200 h-12 rounded-xl"
                  required
                />
              </div>

              <div>
                <Label htmlFor="password" className="text-black font-medium">Senha</Label>
                <Input
                  id="password"
                  name="password"
                  type="password"
                  value={formData.password}
                  onChange={handleChange}
                  className="mt-2 bg-white border-gray-200 h-12 rounded-xl"
                  required
                />
              </div>


              {error && (
                <div className="text-red-500 text-sm bg-red-50 p-3 rounded-lg">
                  {error}
                </div>
              )}

              <Button
                type="submit"
                disabled={loading}
                className="w-full h-12 bg-black text-white hover:bg-gray-800 rounded-xl font-medium"
              >
                {loading ? 'Criando conta...' : 'Criar conta'}
              </Button>

              <div className="relative">
                <div className="absolute inset-0 flex items-center">
                  <div className="w-full border-t border-gray-300"></div>
                </div>
                <div className="relative flex justify-center text-sm">
                  <span className="px-4 bg-gray-50 text-gray-500">Ou continue com</span>
                </div>
              </div>

              <div className="grid grid-cols-2 gap-4">
                {/* Botões de Social Login */}
                <Button
                  type="button"
                  variant="outline"
                  // Implemente handleSocialLogin se o Django suportar OAuth
                  className="h-12 border-gray-200 hover:bg-gray-100 rounded-xl"
                >
                  {/* Google SVG... */}
                  Google
                </Button>
                <Button
                  type="button"
                  variant="outline"
                  // Implemente handleSocialLogin se o Django suportar OAuth
                  className="h-12 border-gray-200 hover:bg-gray-100 rounded-xl"
                >
                  <Github className="w-5 h-5 mr-2" />
                  GitHub
                </Button>
              </div>

              <p className="text-center text-sm text-gray-600">
                Já tem uma conta?{' '}
                <Link href="/login" className="text-[#d946ef] font-medium hover:underline">
                  Entrar
                </Link>
              </p>
            </form>
          </div>
        </div>
      </div>
    </div>
  )
}